     .sidebar {
         background-color: $dark;

         .sidebar-menu {
             .clinicdropdown {
                 a {
                     background-color: #000000;

                     .user-names h5 {
                         color: $white;
                     }
                 }
             }

             ul {
                 >li {
                     ul {
                         li a {
                             span {
                                 color: $dark-text;
                             }
                         }
                     }
                 }
             }

             .menu-arrow {
                 &:before {
                     border-right: 2px solid $dark-text;
                     border-bottom: 2px solid $dark-text;
                 }
