 [data-theme="dark"] {
     body {
         background-color: $dark;
         color: $dark-text;
     }

     h1,
     h2,
     h3,
     h4,
     h5,
     h6,
     .h1,
     .h2,
     .h3,
     .h4,
     .h5,
     .h6 {
         color: $white;
     }

     .header {
         background-color: $dark;

         .header-left {
             background-color: $dark;

             .logo {
                 img {
                     display: none;

                     &.white-logo {
                         display: block;
                     }
                 }
             }

             #toggle_btn {
                 color: $white;
             }
         }

         .dropdown-menu {
             background-color: $dark;
             box-shadow: none;

             a {
                 color: $white;
             }

             .topnav-dropdown-header {
                 border-color: $dark-border;

                 .notification-title {
                     color: $white;
                 }

             }
         }

         .menu-drop-user .dropdown-item {
             border-color: $dark-border;
         }

         .notification-dropdown {
             .notification-list {
                 li {
                     border-bottom: 1px solid $dark-border;

                     .noti-title {
                         color: $white;
                     }

                     .noti-details {
                         color: $white;
                     }

                     p.noti-time {
                         color: $white;
                     }

                     .noti-pdf {
                         .noti-pdf-icon {
                             span {
                                 border: 1px solid $dark-border;
                             }
                         }

                         .noti-pdf-text {
                             p {
                                 color: $white;
                             }

                             span {
                                 color: $dark-textcolor;
                             }
                         }
                     }

                     .notification-time {
                         font-size: $font-size-12;
                         line-height: 1.35;
                         color: $gray-600;
                     }

                 }
             }

             .topnav-dropdown-footer {
                 background: $dark;
             }
         }

         .searchinputs {
             input {
                 border-color: $dark-border;
                 background: transparent;
             }

             .search-addon button {
                 color: $dark-textcolor;
             }
         }

         .menus-info {
             .menu-list {
                 li {
                     a {
                         background: transparent;
                         border-radius: $border-radius-lg;

                         &:hover {
                             background: transparent;
                         }

                         .menu-details {
                             .menu-list-icon {
                                 color: $white;
                             }

                             .menu-details-content {
                                 p {
                                     color: $white;
                                     margin-bottom: 0;
                                 }

                                 span {
                                     font-weight: $font-weight-normal;
                                     color: $dark-textcolor;
                                 }
                             }
                         }
                     }
                 }
             }
         }
     }

     .sidebar {
         background-color: $dark;

         .sidebar-menu {
             .clinicdropdown {
                 a {
                     background-color: #28283E;

                     .user-names h5 {
                         color: $white;
                     }
                 }
             }

             ul {
                 >li {
                     ul {
                         li a {
                             span {
                                 color: $dark-text;
                             }
                         }
                     }
                 }
             }

             .menu-arrow {
                 &:before {
                     border-right: 2px solid $dark-text;
                     border-bottom: 2px solid $dark-text;
                 }
             }
         }
     }

     .contact-grid.proposals-grid .grid-head .table-action .action-icon {
         background: #393947;
         color: $white;
     }

     .contact-grid.proposals-grid .grid-footer {
         background: #171724;
         border-color: #393947;
         color: $white;
     }

     .title-head {
         color: $white;
     }

     .sidebar-themesettings {
         background: $dark;

         .themesettings-header {
             border-color: $dark-border;

             h4 {
                 color: $white;
             }
         }

         .themesettings-inner {
             background: $dark-bg;
             border-color: $dark-border;

             .themesettings-content {
                 border-color: $dark-border;

                 h6 {
                     color: $white;
                 }
             }
         }

         .input-themeselect {
             border-color: $dark-border;
             background: $dark;

             .checkboxs-theme:after {
                 border-color: $dark-border;
             }
         }
     }

     .form-check-label,
     .form-label,
     .sub-header {
         color: $dark-text;
     }

     .form-select {
         option {
             background: transparent;
         }
     }

     .icon-form {
         .form-icon {
             color: $dark-text;
         }
     }

     .drop-down>a,
     .export-dropdwon>a,
     .form-sorts>a {
         border-color: $dark-border;
         box-shadow: none;
         background-color: #1A1A25;
         color: $dark-text;

         &:hover {
             color: $dark-text;
         }
     }

     .manage-dropdwon {
         .dropdown-menu {
             border-color: $dark-border;
             background-color: #1A1A25;
             color: $dark-text;
             box-shadow: none;
         }
     }

     .form-sorts {
         .filter-set-content {
             .filter-set-content-head {
                 a {
                     color: $dark-text;
                 }
             }
         }
     }

     .header-set .select2-container--default {
         .select2-selection--single {
             border-color: $dark-border;
         }
     }

     .form-sorts {
         .filter-dropdown-menu {
             border-color: $dark-border;
             background-color: #1A1A25;
             color: $dark-text;
             box-shadow: none;
         }
     }

     .status-radio:checked+label,
     .status-radio:not(:checked)+label {
         color: $dark-text;
     }

     .filter-set-contents {
         .filter-content-list {
             border-color: $dark-border;
             background-color: #1A1A25;
             color: $dark-text;
         }
     }

     .dataTables_length label .form-select,
     #dataTables_length label .form-select {
         border-color: $dark-border;
         box-shadow: none;
         background-color: #1A1A25;
         color: $dark-text;
     }

     .view-icons {
         border-color: $dark-border;
     }

     .toast {
         background-color: #1A1A25;
     }

     .select2-selection--multiple {
         border-color: $dark-border;
         background: $dark;
         box-shadow: none;
         color: $dark-text;
     }

     .twitter-bs-wizard {
         .twitter-bs-wizard-nav .step-icon {
             border-color: $white;
             color: $white;
         }
     }

     .text-dark {
         color: $dark-text !important;
     }

     .select2-container--default {
         .select2-selection--single {
             border-color: $dark-border;
             background: $dark;
             box-shadow: none;

             .select2-selection__rendered {
                 box-shadow: none;
